<template>
  <div>
    <div class="banner" @click="toggleGallary">
      <img
        class="banner-img"
        :src="img"
      >
      <div class="banner-info">
        <div class="banner-title">{{name}}</div>
        <div class="banner-number">
          <span class="iconfont banner-icon">&#xe674;</span>
          {{imgs.length}}
        </div>
      </div>
    </div>
    <common-animation-fade>
      <keep-alive>
        <common-gallary v-if="isGallaryShow" :imgs="imgs" @click="toggleGallary"></common-gallary>
      </keep-alive>
    </common-animation-fade>
  </div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import CommonAnimationFade from 'common/animation/Fade'
export default {
  name: 'DetailBanner',
  props: {
    name: String,
    img: String,
    imgs: Array
  },
  components: {
    CommonGallary,
    CommonAnimationFade
  },
  data () {
    return {
      isGallaryShow: false
    }
  },
  methods: {
    toggleGallary () {
      this.isGallaryShow = !this.isGallaryShow
    }
  }
}
</script>

<style lang="stylus" scoped>
  .banner
    position: relative
    height: 55vw
    overflow: hidden
    .banner-img
      width: 100%
    .banner-info
      display: flex
      align-items: center
      position: absolute
      right: 0
      bottom: 0
      left: 0
      padding: 0 .2rem .2rem
      line-height: .6rem
      color: #fff
      background-image: linear-gradient(to top, rgba(0, 0, 0, .7), rgba(0, 0, 0, 0) 40%)
      .banner-title
        flex: 1
        font-size: .32rem
      .banner-number
        padding: 0 .1rem
        height: .4rem
        line-height: .4rem
        border-radius: .14rem
        font-size: .24rem
        background: rgba(0, 0, 0, .6)
        .banner-icon
          font-size: .26rem
</style>



