<template>
  <div class="header">
    <div class="header-left">
      <div class="iconfont back-icon">&#xe624;</div>
    </div>
    <div class="header-input">
      <span class="iconfont">&#xe632;</span>
      输入城市/景点/游玩主题
    </div>
    <router-link to="/city">
      <div class="header-right">
        <div class="header-city">{{this.city}}</div>
        <span class="iconfont arrow-icon">&#xe64a;</span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

// scope属性可以限制style的作用范围
<style lang="stylus" scoped>
  // ~styles相当于styles目录,别名可以在webpack.base.conf.js里面定义
  @import '~styles/varibles.styl'
  .header
    display: flex
    height: $headerHeight
    line-height: $headerHeight
    background: $bgColor
    color: #fff
    .header-left
      width: .64rem
      // float: left
      .back-icon
        font-size: .3rem
        font-weight: bold
        text-align: center
    .header-input
      flex: 1
      height: .62rem
      line-height: .62rem
      margin-top: .12rem
      margin-left: .1rem
      padding-left: .2rem
      background: #fff
      border-radius: .06rem
      color: #ccc
    .header-right
      display: flex
      padding: 0 .1rem
      max-width: 1.37rem
      color: #fff
      .header-city
        flex: 1
        padding-right: .1rem
        text-align: center
        overflow: hidden
        white-space: nowrap
        text-overflow: ellipsis
      .arrow-icon
        flex: none
        margin-left: -.04rem
        font-size: .24rem
</style>



