<template>
  <div>
    <detail-header :name="sightName"></detail-header>
    <detail-banner :name="sightName" :img="bannerImg" :imgs="gallaryImgs"></detail-banner>
    <detail-list :list="categoryList"></detail-list>
    <div style="height: 1000vh"></div>
  </div>
</template>

<script>
import axios from 'axios'
import DetailBanner from './components/Banner'
import DetailHeader from './components/Header'
import DetailList from './components/List'
export default {
  name: 'Detail',
  components: {
    DetailBanner,
    DetailHeader,
    DetailList
  },
  data () {
    return {
      sightName: '',
      bannerImg: '',
      gallaryImgs: [],
      categoryList: []
    }
  },
  methods: {
    getDetailInfo () {
      axios.get('api/detail.json'
      // {
      //   params: {
      //     id: this.$route.params.id
      //   }
      // }
      )
        .then(this.handleGetInfoSucceed)
        .catch(this.handleGetInfoFail)
    },
    handleGetInfoSucceed (response) {
      response = response.data
      if (response.ret) {
        let data = response.data
        this.sightName = data.sightName
        this.bannerImg = data.bannerImg
        this.gallaryImgs = data.gallaryImgs
        this.categoryList = data.categoryList
      }
    },
    handleGetInfoFail (response) {
      console.warn('Fail')
      console.warn(response)
    }
  },
  mounted () {
    this.getDetailInfo()
  }
}
</script>

<style lang="stylus" scoped>

</style>



