// 路由就是根据网址的不同返回不同的内容给用户
<template>
  <div id="app">
    <!-- 显示的但是当前路由地址所对应的内容 -->
    <keep-alive exclude="Detail">
      <router-view/>
    </keep-alive>
  </div>
</template>

<script>
export default {
  name: 'App'
}
</script>

