<template>
  <div>
    <div class="title">周末去哪儿</div>
    <ul>
      <li class="item border-bottom" v-for="item of list" :key="item.id">
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl">
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  },
  data: function () {
    return {}
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    line-height: .8rem
    background: #eee
    text-indent: .2rem
  ul
    background: #eee
    .item
      background: #fff
      margin-bottom: .1rem
      .item-img-wrapper
        height: 37.4375vw
        background: #ddd
        .item-img
          width: 100%
      .item-info
        padding: .2rem
        .item-title
          line-height: .48rem
          font-size: .28rem
          ellipsis()
        .item-desc
          line-height: .42rem
          font-size: .24rem
          color: #888
          ellipsis()
</style>



