<template>
  <div>
    <div class="search">
      <input
        class="search-input"
        type="text"
        placeholder="输入城市名或拼音"
        v-model.trim="keyword"
        v-on="$listeners"
      >
    </div>
    <div class="search-content" ref="wrapper" v-show="isUlShow">
      <ul>
        <li
          class="search-item border-bottom"
          v-for="city of matchedCities"
          :key="city.id"
          @click="handleCityClick(city.name)"
        >{{city.name}}</li>
        <li class="search-item border-bottom" v-show="hasNoData">没有找到该城市</li>
      </ul>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import BScroll from 'better-scroll'
export default {
  name: 'CitySearch',
  props: {
    cities: Object
  },
  data () {
    return {
      keyword: '',
      timer: null,
      matchedCities: []
    }
  },
  watch: {
    keyword () {
      if (this.timer) {
        clearTimeout(this.timer)
      }
      this.timer = setTimeout(() => {
        let list = []
        if (this.keyword === '' || Object.keys(this.cities).length === 0) {
          list = []
        } else if (/^[a-zA-Z]+$/.test(this.keyword)) {
          for (const cityGroup of Object.values(this.cities)) {
            for (const city of cityGroup) {
              if (city.spell.includes(this.keyword)) {
                list.push(city)
              }
            }
          }
          list.sort((prev, next) => {
            let spellCompare = prev.spell.indexOf(this.keyword) - next.spell.indexOf(this.keyword)
            let localeCompare = prev.name.localeCompare(next.name)
            if (spellCompare > 0) {
              return 1
            } else if (spellCompare === 0) {
              return localeCompare
            } else {
              return -1
            }
          })
        } else {
          for (const cityGroup of Object.values(this.cities)) {
            for (const city of cityGroup) {
              if (city.name.includes(this.keyword)) {
                list.push(city)
              }
            }
          }
          list.sort((prev, next) => {
            let initialCompare = prev.name.indexOf(this.keyword[0]) - next.name.indexOf(this.keyword[0])
            let localeCompare = prev.name.localeCompare(next.name)
            if (initialCompare > 0) {
              return 1
            } else if (initialCompare === 0) {
              return localeCompare
            } else {
              return -1
            }
          })
        }
        this.matchedCities = list
        this.timer = null
      }, 100)
    }
  },
  computed: {
    isUlShow () {
      return this.keyword.length > 0
    },
    hasNoData () {
      return this.keyword.length === 0
    }
  },
  methods: {
    ...mapMutations(['changeCity']),
    handleCityClick (city) {
      this.changeCity(city)
      this.$router.push('/')
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper, {
      bounce: false,
      swipeBounceTime: 100,
      click: true
    })
  },
  updated () {
    this.scroll.refresh()
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .border-bottom
    &:before
      border-color: #ccc
  .search
    overflow: hidden
    background: $bgColor
    text-align: center
    padding: 0 .1rem
    .search-input
      box-sizing: border-box
      margin: 0 auto .1rem
      padding: 0 .2rem
      width: 100%
      border-radius: .04rem
      color: #666
      font-size: .26rem
      text-align: center
      line-height: 0.6rem
  .search-content
    overflow: hidden
    position: absolute
    top: 1.56rem
    right: 0
    bottom: 0
    left: 0
    background: #fff
    z-index: 1
    .search-item
        line-height: .76rem
        padding-left: .2rem
</style>



