<template>
  <div class="city">
    <city-header class="noflex"></city-header>
    <city-search class="noflex" :cities="data.cities" @focus="focus" @blur="blur"></city-search>
    <city-list class="flex" v-bind="data" :letter="letter"></city-list>
    <city-alphabet v-show="showAlphabet" :cities="data.cities" @change="handleLetterChange"></city-alphabet>
  </div>
</template>

<script>
import axios from 'axios'
import CityHeader from './components/Header'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'

export default {
  name: 'City',
  components: {
    CityHeader,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data () {
    return {
      data: {},
      letter: '',
      showAlphabet: true
    }
  },
  methods: {
    getCityInfo () {
      axios.get('/api/city.json')
        .then(this.getCityInfoSucceed)
        .catch(this.getCityInfoFail)
    },
    getCityInfoSucceed (res) {
      res = res.data
      if (res.ret) {
        this.data = res.data
      }
    },
    getCityInfoFail (err) {
      console.warn(err)
    },
    handleLetterChange (letter) {
      this.letter = letter
    },
    focus () {
      this.showAlphabet = false
    },
    blur () {
      setTimeout(() => {
        this.showAlphabet = true
      }, 100)
    }
  },
  mounted () {
    this.getCityInfo()
  }
}
</script>

<style lang="stylus" scoped>
  .city
    height: 100vh
    display: flex
    flex-direction: column
    .noflex
      flex: none
    .flex
      flex: 1 1 auto
      min-height: 0
</style>



