<template>
  <div class="container">
    <div class="item border-bottom" v-for="(item, index) of list" :key="index">
      <div class="item-title">
        <span class="item-title-icon"></span>
        {{item.title}}
        <detail-list  v-if="item.children" :list="item.children"></detail-list>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    list: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {}
  },
  computed: {},
  methods: {}
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .container
    position: relative
    top: -.1rem
    background: #fff
    border-radius: .1rem
    box-shadow: 0 0.04rem 0.12rem 0 rgba(0,0,0,0.10)
    .item-title
      line-height: .8rem
      font-size: .32rem
      padding: 0 .2rem
      .item-title-icon
        display inline-block
        background-image: linear-gradient(126deg,#00e0ca 0,#00bfd4 100%);
        border-radius: .09rem;
        height: .24rem;
        min-width: .09rem;
        margin-right: .04rem;
</style>



