<template>
  <ul class="list" v-if="cities" ref="ul">
    <li
      class="item"
      v-for="letter in letterList"
      :key="letter"
      @touchstart.prevent="handleLetterTouchstart"
      @touchmove.prevent="handleLetterTouchmove"
    >
      {{letter}}
    </li>
  </ul>
</template>

<script>
export default {
  name: 'CityAlphabet',
  props: {
    cities: Object
  },
  data () {
    return {
      startY: 0,
      elementHeight: 20,
      timer: null
    }
  },
  computed: {
    letterList () {
      let list = Object.keys(this.cities)
      list.unshift('#')
      return list
    }
  },
  updated () {
    let firstLi = this.$refs.ul.firstElementChild
    this.startY = firstLi.offsetTop + 78
    this.elementHeight = parseFloat(window.getComputedStyle(firstLi).height.slice(0, -2))
  },
  methods: {
    handleLetterTouchstart (event) {
      let startLetter = event.target.innerText
      this.$emit('change', startLetter)
    },
    handleLetterTouchmove (event) {
      // 通过触摸点相对与屏幕的位置减去第一个首字母li相对于屏幕的位置判断划到了那个字母
      // 通过计时器对touchmove事件节流,优化性能
      if (!this.timer) {
        this.timer = setTimeout(() => {
          let nowY = event.touches[0].clientY
          let deltaIndex = Math.floor((nowY - this.startY) / this.elementHeight)
          let nowLetterIndex = Math.min(this.letterList.length - 1, Math.max(0, deltaIndex))
          let nowLetter = this.letterList[nowLetterIndex]
          this.$emit('change', nowLetter)
          this.timer = null
        }, 16)
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .list
    position: absolute
    top: 1.56rem
    right: 0
    bottom: 0
    width: .4rem
    display: flex
    flex-direction: column
    justify-content: center
    overflow: hidden
    .item
      line-height: .4rem
      text-align: center
      font-family: monospace
      color: $bgColor
</style>



