<template>
  <div class="container">
    <div class="header" v-show="isHeaderShow" :style="opacity">
      <router-link to="/">
        <div class="header-back">
          <div class="iconfont back-icon">&#xe624;</div>
        </div>
      </router-link>
      <h1 class="header-title">{{name}}</h1>
    </div>
    <div class="header-mini" v-show="isHeaderMiniShow">
      <router-link to="/">
        <div class="header-back">
          <span class="iconfont back-icon">&#xe624;</span>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: 'DetailHeader',
  props: {
    name: String
  },
  data () {
    return {
      isHeaderShow: false,
      opacity: {
        'opacity': 0
      },
      timer: null
    }
  },
  computed: {
    isHeaderMiniShow () {
      return !this.isHeaderShow
    }
  },
  methods: {
    toggleHeader (event) {
      if (!this.timer) {
        this.timer = setTimeout(() => {
          if (window.scrollY === 0) {
            this.isHeaderShow = false
          } else {
            this.isHeaderShow = true
          }
          if (window.scrollY > 0 && window.scrollY <= 200) {
            let opacity = window.scrollY / 200
            this.opacity = { opacity }
          }
          this.timer = null
        }, 8)
      }
    }
  },
  mounted () {
    this.isHeaderShow = false
    window.addEventListener('scroll', this.toggleHeader)
  },
  destroyed () {
    window.removeEventListener('scroll', this.toggleHeader)
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .container
    position: fixed
    top: 0
    z-index: 1
    .header
      overflow: hidden
      width: 100vw
      height: $headerHeight
      line-height: $headerHeight
      text-align: center
      background: $bgColor
      opacity: 0
      display: flex
      color: #fff
      .header-back
        flex: none
        width: .64rem
        color: #fff
        .back-icon
          font-size: .3rem
          font-weight: bold
          text-align: center
      .header-title
        flex: 1
        height: .62rem
        line-height: .62rem
        margin-top: .12rem
        margin-right: .64rem
        font-size: .32rem
    .header-mini
      overflow: hidden
      height: $headerHeight
      .header-back
        width: .64rem
        height: .64rem
        line-height: .64rem
        margin: .11rem 0 0 .05rem
        background: rgba(0, 0, 0, .3)
        border-radius: 100%
        text-align: center
        color: #fff
        .back-icon
          position: relative
          left: -.035rem
          font-size: .3rem
          font-weight: bold
</style>



