<template>
  <div>
    <div class="recommend-title">热销推荐</div>
    <ul>
      <router-link
        tag="li"
        class="item border-bottom"
        v-for="item of list"
        :key="item.id"
        :to="'/detail/' + item.id"
      >
        <img class="item-img" :src="item.imgUrl">
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
          <button class="item-button">查看详情</button>
        </div>
      </router-link>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'HomeRecommend',
  props: {
    list: Array
  },
  data: function () {
    return {}
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .recommend-title
    margin-top: .2rem
    line-height: .8rem
    background: #eee
    text-indent: .2rem
  .item
    display: flex
    height: 2.1rem
    .item-img
      width: 1.7rem
      height: 1.7rem
      padding: .2rem
    .item-info
      flex: 1
      padding: .2rem .2rem .2rem 0
      // flex不会缩小到小于min-width
      min-width: 0
      .item-title
        line-height: .54rem
        font-size: .32rem
        ellipsis()
      .item-desc
        font-size: .24rem
        line-height: .4rem
        color: #ccc
        ellipsis()
      .item-button
        background: #ff9300
        color: #fff
        font-size: .24rem
        line-height: .42rem
        margin-top: .2rem
        padding: 0 .1rem
        border-radius: .06rem
</style>



