<template>
  <div class="header">
    <router-link to="/">
      <div class="header-back">
        <div class="iconfont back-icon">&#xe624;</div>
      </div>
    </router-link>
    <h1 class="header-title">选择城市</h1>
  </div>
</template>

<script>
export default {
  name: 'CityHeader'
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .header
    overflow: hidden
    height: $headerHeight
    line-height: $headerHeight
    text-align: center
    background: $bgColor
    display: flex
    color: #fff
    .header-back
      flex: none
      width: .64rem
      color: #fff
      .back-icon
        font-size: .3rem
        font-weight: bold
        text-align: center
    .header-title
      flex: 1
      height: .62rem
      line-height: .62rem
      margin-top: .12rem
      margin-right: .64rem
      font-size: .32rem
</style>



