<template>
  <div class="list" ref="wrapper">
    <div class="content">
      <div class="area current" ref="#">
        <div class="title">当前城市</div>
        <ul class="button-list">
          <li
            class="button"
            @click="handleCityClick(city)"
          >{{city}}</li>
        </ul>
      </div>
      <div class="area hot">
        <div class="title">热门城市</div>
        <ul class="button-list">
          <li
            class="button"
            v-for="item of hotCities"
            :key="item.id"
            @click="handleCityClick(item.name)"
          >
            {{item.name}}
          </li>
        </ul>
      </div>
      <div
        class="area"
        v-for="(array, initial) in cities"
        :key="initial"
        :ref="initial"
      >
        <div class="title">{{initial}}</div>
        <ul class="item-list">
          <li
            class="item border-bottom"
            v-for="city of array"
            :key="city.id"
            @click="handleCityClick(city.name)"
          >{{city.name}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import BScroll from 'better-scroll'
export default {
  name: 'CityList',
  props: {
    hotCities: Array,
    cities: Object,
    letter: String
  },
  watch: {
    letter (value) {
      this.scroll.scrollToElement(this.$refs[value][0] || this.$refs[value])
    }
  },
  computed: {
    ...mapState({
      city: 'city'
    }),
    show () {
      return this.city.length !== 0
    }
  },
  methods: {
    ...mapMutations(['changeCity']),
    handleCityClick (city) {
      // 简单的传递可以用commit
      // this.$store.dispatch('changeCity', city)
      this.changeCity(city)
      this.$router.push('/')
    }
  },
  mounted () {
    this.scroll = new BScroll(this.$refs.wrapper, {
      bounce: false,
      swipeBounceTime: 100,
      click: true
    })
  },
  updated () {
    this.scroll.refresh()
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  .border-topbottom
    &:before
      border-color: #ccc
    &:after
      border-color: #ccc
  .border-bottom
    &:before
      border-color: #ccc
  .list
    overflow: hidden
    .title
      padding-left: .2rem
      line-height: .54rem
      font-size: .26rem
      background: #eee
      color: #666
    .button-list
      padding: .2rem .6rem .2rem .2rem
      display: grid
      grid-template-columns: repeat(3, 1fr)
      grid-gap: .2rem
      .button
        padding: .1rem 0
        text-align: center
        border: .02rem solid #ccc
        border-radius: .06rem
    .item-list
      .item
        line-height: .76rem
        padding-left: .2rem
</style>



