<template>
  <div class="container" v-on="$listeners">
    <div class="wrapper">
      <swiper :options="swiperOption">
        <swiper-slide v-for="(img, index) in imgs" :key="index">
          <img class="swiper-img swiper-lazy" :data-src="img">
        </swiper-slide>
      </swiper>
      <div class="swiper-pagination"  slot="pagination"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonGallary',
  props: {
    imgs: {
      type: Array,
      default () {
        return []
      }
    }
  },
  data () {
    return {
      swiperOption: {
        pagination: {
          el: '.swiper-pagination',
          type: 'fraction'
        },
        loop: true,
        lazy: {
          loadPrevNext: true
        }
      }
    }
  },
  activated () {
    this.$children[0].swiperInstance.slideTo(1, 0)
  }
}
</script>

<style lang="stylus" scoped>
  .wrapper >>> .swiper-wrapper
    align-items: center
  .container
    display: flex
    flex-direction: column
    justify-content: center
    position: fixed
    top: 0
    right: 0
    bottom: 0
    left: 0
    z-index: 1
    background: #000
    .wrapper
      .swiper-img
        width: 100%
      .swiper-pagination
        color: #fff
</style>



