<template>
  <swiper class="icons-swipper" :options='swiperOption'>
    <!-- 只是显示没有变更的话,key=index也没啥问题 -->
    <swiper-slide v-for="(iconList, index) of iconPages" :key="index">
      <div class='icons'>
        <div class='icon' v-for="icon of iconList" :key="icon.id">
          <div class='icon-img-container'>
            <img class='icon-img' :src='icon.src' alt=''>
          </div>
          <p class='icon-title' :title="icon.title">{{icon.title}}</p>
        </div>
      </div>
    </swiper-slide>
  </swiper>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    list: Array
  },
  data: function () {
    return {
      swiperOption: {
      }
    }
  },
  computed: {
    iconPages () {
      let pages = []
      this.list.forEach((icon, index) => {
        let page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(icon)
      })
      return pages
    }
  }
}
</script>

<style lang='stylus' scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
  .icons-swipper
    margin-top: .1rem
    width: 100%
    max-height: 3.32rem
    .icons
      overflow: hidden
      display: grid
      grid-template-columns: repeat(4, 25vw)
      grid-template-rows: repeat(2, minmax(1.66rem, 1fr))
      .icon
        display: flex
        flex-direction: column
        .icon-img-container
          flex: 1
          position: relative
          margin: .1rem
          .icon-img
            position: absolute
            height: 100%
            max-height: 1.2rem
            top: 0
            left: 0
            bottom: 0
            right: 0
            margin: auto
        .icon-title
          flex: none
          font-size: .28rem
          height: .44rem
          // line-height: .44rem
          padding: 0 .1rem
          text-align: center
          color: $darkTextColor
          ellipsis()
</style>



